import React from "react"
import styles from "../../styles/HomePage.module.css"
export function Jobs() {
    return <section className={styles.job}>
        <div className={styles.job_title}>
            <h1>Get instant job alerts from top companies</h1>
            <p>Be the first one to apply to jobs</p>
        </div>
        <div className={styles.job_container}>
            <div className={styles.job_container_left}>
                <img src={process.env.PUBLIC_URL + '/app_base.png'} alt=""/>
            </div>
            <div className={styles.job_container_right}>
                <div className={styles.job_mobile}>
                        <div>91 <input type="text" placeholder="Mobile"/></div>
                        <button>Get App</button>
                </div>
                <div className={styles.job_rating}>
                    <p>Rated</p>
                    <span>
                    <img src="data:image/png;base64," alt="" /></span>
                    <p>|</p>
                    <strong>10 million</strong>
                    <p>downloads</p>
                </div>
                <div className={styles.job_button}>
                    <button> <img src={process.env.PUBLIC_URL + '/playstore.png'} alt=""/> <span>Google Play</span></button>
                    <button> <img src={process.env.PUBLIC_URL + '/appstore.png'} alt=""/> App Store</button>
                </div>
            </div>
            </div>
    </section>
}