import React from "react"
import styles from "../../styles/Register.module.css"
import axios from "axios"
export function RegisterContainer({login}) {
    const payload = {
        name: "",
        email: "",
        mobile: "",
        password: "",
    }
    const [userData, setUserData] = React.useState({...payload});
    const HandleChange = (e) => {
        setUserData({ ...userData, [e.target.name]: e.target.value });
    }
    const HandlePost = () => {
        try {
            axios.post("http://localhost:3004/users", userData);
        } catch (e) {
            console.log(e);
        }
        
    }
    const HandleContinue = () => {
        if (userData.name.length !== 0 && userData.email.length !== 0 && String(userData.mobile).length === 10 && String(userData.password).length >= 8) {
            HandlePost();
            alert("You have sucessfully registered")
            setTimeout(() => {
                login();
            },1000)
            
        } else if (userData.name.length === 0) {
            alert("Enter UserName !!!")
        }
            else if (userData.email.length === 0) {
            alert("Enter Email !!!")
        }
             else if (String(userData.mobile).length !== 10) {
            alert("Invalid Mobile No. !!!")
        }
        else if (String(userData.password).length < 8) {
            alert("Password should contain atleast 8 characters !!!")
        }
        else {
            alert("Invalid Credentials !!!")
        }
    }
    return <section className={styles.reg_container}>
        <div className={styles.reg_body}>
            <div className={styles.reg_container_left}>
                <h3>
                    Register with Resume
                </h3>
                <h4>We will autofill your details</h4>
                <button>Upload resume</button>
                <p>Supported formats- pdf, doc, docx, csv, rtf, txt files File size should not exceed 5 MB</p>
            </div>
            <div className={styles.reg_container_right}>
                <h3>
                    Lets get started, tell us about yourself.
                </h3>
                <h4>It only takes a couple of minutes to get started</h4>
                <div>
                    <input value={userData.name} type="text" name="name" onChange={HandleChange} placeholder="Name"/>
                </div>
                <div>
                    <input type="email" name="email" value={userData.email} onChange={HandleChange} placeholder="Email"/>
                </div>
                <div>
                    <p>91</p>
                    <input type="Number" name="mobile" value={userData.mobile} onChange={HandleChange} placeholder="Mobile Number"/>
                </div>
                <div>
                    <input type="password" name="password" value={userData.password} onChange={HandleChange} placeholder="Password"/>
                </div>
                <div>
                    <button onClick={HandleContinue}>Continue &rarr;</button>
                </div>
                <div>

                    <h4>or Register with</h4>
                </div>
                <div>
                    <div>
                        <img src="data:image/png;base64,"alt=""/>
                        <p>Google</p>
                    </div>
                    <div>
                        <img src="data:image/png;base64,"alt=""/>
                        <p>Linkedin</p>
                    </div>
                    <div>
                        <img src="https://icon-library.com/images/facebook-svg-icon/facebook-svg-icon-29.jpg"alt=""/>
                        <p>Facebook</p>
                    </div>
                </div>
            </div>
            </div>
       
    </section>
}